:host {
  --pfe-jump-links-panel__section--spacer: calc(var(--pfe-jump-links-nav--Height--actual) + var(--pfe-navigation--Height--actual) + var(--jump-links-nav--nudge));
  --context: var(--pfe-jump-links--context);
  box-sizing: border-box;
  font-family: var(--pfe-theme--font-family, "Red Hat Text", "RedHatText", "Overpass", Overpass, Arial, sans-serif);
  font-weight: var(--pfe-theme--font-weight--normal, 400);
  font-weight: 400;
  display: block;
  position: sticky;
  top: var(--pfe-jump-links__nav--offset, calc(var(--pfe-theme--container-spacer, 1rem) * 4));
  padding-left: 0;
  background-color: var(--pfe-jump-links--BackgroundColor, transparent);
}

@media (min-width: 992px) {
  :host {
    border: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) transparent;
    padding: var(--pfe-jump-links__nav--Padding, 0);
  }
}

:host .pfe-jump-links-nav__heading > h3,
:host ::slotted([slot="pfe-jump-links-nav--heading"]) {
  --pf-c--FontSize: var(--pfe-jump-links__heading--FontSize, var(--pf-global--FontSize--sm, 0.875rem));
  color: var(--pfe-broadcasted--text, #3c3f42);
  margin: 0 0 var(--pfe-theme--container-spacer, 1rem);
  font-size: var(--pfe-jump-links__heading--FontSize, var(--pf-global--FontSize--sm, 0.875rem));
  font-weight: 400;
  text-transform: uppercase;
}

@media (max-width: 991px) {
  :host .pfe-jump-links-nav__heading > h3,
  :host ::slotted([slot="pfe-jump-links-nav--heading"]) {
    display: none;
  }
}

:host([sticky="false"]) {
  position: relative;
}

nav {
  visibility: visible;
  margin: 0;
  list-style: none;
  padding: 0;
}

nav ul {
  padding: 0;
  border-left: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) var(--pfe-theme--color--ui-disabled, #d2d2d2);
}

nav li {
  border-left: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) transparent;
  display: block;
  text-decoration: none;
  margin-left: calc(var(--pfe-theme--surface--border-width, 1px) * -1);
}

nav li.expand ul {
  flex: 1;
}

nav li.expand .sub-section {
  display: table;
  height: auto;
}

nav li[active] {
  border-left-color: var(--pfe-jump-links--BorderColor, var(--pfe-theme--color--ui-accent, #06c));
}

nav a {
  position: relative;
  display: table;
  color: var(--pfe-theme--color--ui-disabled--text, #6a6e73);
  font-size: var(--pfe-jump-links--FontSize, var(--pf-global--FontSize--md, 1rem));
  text-decoration: none;
  line-height: var(--pfe-theme--line-height, 1.5);
  padding: calc(var(--pfe-jump-links__link--vertical-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 6)) * 2) calc(var(--pfe-jump-links__link--horizontal-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 3)) * 2);
}

nav a.has-sub-section {
  padding-bottom: var(--pfe-jump-links__link--vertical-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 6));
}

nav a.sub-section {
  margin-left: calc(var(--pfe-theme--container-spacer, 1rem) * 0.75);
  font-size: calc(var(--pfe-jump-links--FontSize, var(--pf-global--FontSize--md, 1rem)) * 0.85);
  padding: var(--pfe-jump-links__link--vertical-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 6)) calc(var(--pfe-jump-links__link--horizontal-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 3)) * 2);
}

@media (min-width: 992px) {
  nav a.sub-section {
    display: none;
  }
}

nav a.sub-section:last-child {
  padding-bottom: var(--pfe-jump-links__link--horizontal-spacer, calc(var(--pfe-theme--content-spacer, 1.5rem) / 3));
}

nav a::after {
  content: "";
  position: absolute;
  width: calc(100% - var(--pfe-theme--surface--border-width--heavy, 4px));
  height: calc(100% - var(--pfe-theme--surface--border-width--heavy, 4px));
  top: 0;
  left: 0;
}

nav a:focus {
  outline: none;
}

nav a:focus::after {
  border-radius: var(--pfe-theme--surface--border-radius, 3px);
  border: 2px var(--pfe-theme--surface--border-style, solid) var(--pfe-theme--color--link, #06c);
}

nav a:hover {
  color: var(--pfe-theme--color--surface--darkest, #151515);
}

nav a[active] {
  color: var(--pfe-theme--color--surface--darkest, #151515);
}

nav ul ul {
  display: flex;
  flex-direction: column;
  flex: 0;
  margin: 0;
  padding: 0;
  border-left: none !important;
  overflow-y: hidden;
  transition: flex 1s linear;
}

nav ul ul li {
  border-left: none !important;
}

:host {
  --pfe-accordion__panel-container--Padding: 0;
}

@media (max-width: 767px) {
  :host {
    --pfe-accordion__panel-container--Padding: 20px;
  }
}

pfe-accordion pfe-accordion-header {
  --pfe-theme--color--surface--border: transparent;
  --pfe-theme--color--ui-base--on-dark: transparent;
  --pfe-theme--color--ui-base: transparent;
  position: sticky;
  top: 0;
}

pfe-accordion pfe-accordion-panel {
  border-left-color: transparent;
  border-right: none;
}

@media (max-width: 991px) {
  pfe-accordion {
    border: var(--pfe-theme--surface--border-width, 1px) var(--pfe-theme--surface--border-style, solid) var(--pfe-theme--color--ui-disabled, #d2d2d2);
  }
  pfe-accordion pfe-accordion-header {
    background-color: var(--pfe-theme--color--surface--lightest, #fff);
    --context: var(--pfe-theme--color--surface--lightest--context, light);
  }
}

@media (min-width: 992px) {
  pfe-accordion pfe-accordion-header {
    visibility: collapse !important;
    display: none;
  }
  pfe-accordion pfe-accordion-panel {
    visibility: collapse;
    display: none;
    opacity: 1;
    display: block;
  }
  pfe-accordion pfe-accordion-panel > pfe-jump-links-nav {
    width: 100%;
    display: block;
    visibility: visible;
  }
}

:host([color="darkest"]) button[aria-expanded="true"] {
  --pfe-jump-links--context: var(--pfe-theme--color--surface--darkest--context, dark);
  background-color: var(--pfe-jump-links--BackgroundColor, var(--pfe-theme--color--surface--darkest, #151515));
}

:host([color="darkest"]) .pfe-jump-links-nav__heading > h3,
:host([color="darkest"]) ::slotted([slot="pfe-jump-links-nav--heading"]) {
  color: var(--pfe-theme--color--text--on-dark, #fff);
}

:host([color="darkest"]) ul {
  border-left-color: var(--pfe-theme--color--text--muted, #6a6e73);
}

:host([color="darkest"]) ul li[active] {
  border-left-width: var(--pfe-theme--ui--border-width--active, 3px);
  border-left-color: var(--pfe-theme--color--link--on-dark, #73bcf7);
}

:host([color="darkest"]) ul a {
  color: var(--pfe-theme--color--text--muted--on-dark, #d2d2d2);
}

:host([color="darkest"]) ul a:hover {
  color: var(--pfe-theme--color--text--on-dark, #fff);
}

:host([color="darkest"]) ul a[active] {
  color: var(--pfe-theme--color--text--on-dark, #fff);
}

:host([color="darkest"]) ul a:focus::after {
  border-color: var(--pfe-theme--color--link--on-dark, #73bcf7);
}

:host([color="accent"]) button[aria-expanded="true"] {
  --pfe-jump-links--context: var(--pfe-theme--color--surface--accent--context, saturated);
  background-color: var(--pfe-jump-links--BackgroundColor, var(--pfe-theme--color--surface--accent, #004080));
}

:host([color="accent"]) .pfe-jump-links-nav__heading > h3,
:host([color="accent"]) ::slotted([slot="pfe-jump-links-nav--heading"]) {
  color: var(--pfe-theme--color--text--on-saturated, #fff);
}

:host([color="accent"]) ul {
  border-left-color: var(--pfe-theme--color--text--muted, #6a6e73);
}

:host([color="accent"]) ul li[active] {
  border-left-width: var(--pfe-theme--ui--border-width--active, 3px);
  border-left-color: var(--pfe-theme--color--link--on-saturated, #fff);
}

:host([color="accent"]) ul a {
  color: var(--pfe-theme--color--text--muted--on-saturated, #d2d2d2);
}

:host([color="accent"]) ul a:hover {
  color: var(--pfe-theme--color--text--on-saturated, #fff);
}

:host([color="accent"]) ul a[active] {
  color: var(--pfe-theme--color--text--on-saturated, #fff);
}

:host([color="accent"]) ul a:focus::after {
  border-color: var(--pfe-theme--color--link--on-saturated, #fff);
}

:host([horizontal]) {
  padding: 0;
  top: var(--pfe-jump-links-panel--offset, 0);
  width: 100%;
}

:host([horizontal]) nav {
  min-height: calc(var(--pfe-theme--container-spacer, 1rem) * 5.5);
  background-color: var(--pfe-theme--color--surface--lighter, #f0f0f0);
  --context: var(--pfe-theme--color--surface--lighter--context, light);
  display: flex;
  position: relative;
}

:host([horizontal]) nav .pfe-jump-links-nav__heading {
  display: none;
}

:host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--logo"]) {
  position: absolute;
  left: 0;
  max-height: calc(var(--pfe-theme--container-spacer, 1rem) * 3.5);
  max-width: calc(var(--pfe-theme--container-spacer, 1rem) * 14);
  top: calc(var(--pfe-theme--container-spacer, 1rem) * 1);
}

@media (max-width: 991px) {
  :host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--logo"]) {
    left: calc(var(--pfe-theme--container-spacer, 1rem) * 2);
  }
}

@media (min-width: 992px) {
  :host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--logo"]) {
    left: calc(var(--pfe-theme--container-spacer, 1rem) * 4);
  }
}

:host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--cta"]) {
  position: absolute;
  top: calc(var(--pfe-theme--container-spacer, 1rem) * 1);
}

@media (max-width: 991px) {
  :host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--cta"]) {
    right: calc(var(--pfe-theme--container-spacer, 1rem) * 2);
  }
}

@media (min-width: 992px) {
  :host([horizontal]) nav ::slotted([slot="pfe-jump-links-nav--cta"]) {
    right: calc(var(--pfe-theme--container-spacer, 1rem) * 4);
  }
}

:host([horizontal]) #container {
  flex: 1;
  display: flex;
  justify-items: center;
  flex-direction: column;
}

:host([horizontal]) ul {
  border: none;
  text-align: center;
  flex: 1;
  display: flex;
  padding: 0;
  width: auto;
  margin: 0 auto;
}

:host([horizontal]) ul li {
  padding: 0 calc(var(--pfe-theme--container-spacer, 1rem) * 1.5);
  display: flex;
  height: calc(var(--pfe-theme--container-spacer, 1rem) * 5);
  align-items: center;
  border-top: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) transparent;
  border-bottom: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) transparent;
  border-left: none;
  border-right: none;
}

:host([horizontal]) ul li[active] {
  border-top: var(--pfe-theme--surface--border-width--heavy, 4px) var(--pfe-theme--surface--border-style, solid) var(--pfe-theme--color--ui-accent, #06c);
}

:host([horizontal]) ul a {
  text-decoration: none;
  color: var(--pfe-theme--color--ui-disabled--text, #6a6e73);
}

:host([horizontal]) ul a[active] {
